$icons: (0:0);
$icons: map-merge($icons,('computer': (X: -60px, Y:-56px, W: 41px, H: 45px, TW: 203px, TH: 154px, IMG: '../img/sprite.png')));
$icons: map-merge($icons,('credit-card': (X: 0px, Y:-124px, W: 46px, H: 30px, TW: 203px, TH: 154px, IMG: '../img/sprite.png')));
$icons: map-merge($icons,('help': (X: -60px, Y:0px, W: 46px, H: 46px, TW: 203px, TH: 154px, IMG: '../img/sprite.png')));
$icons: map-merge($icons,('mobile-phone': (X: 0px, Y:-56px, W: 39px, H: 58px, TW: 203px, TH: 154px, IMG: '../img/sprite.png')));
$icons: map-merge($icons,('plug': (X: -167px, Y:-54px, W: 23px, H: 42px, TW: 203px, TH: 154px, IMG: '../img/sprite.png')));
$icons: map-merge($icons,('settings': (X: -116px, Y:0px, W: 41px, H: 41px, TW: 203px, TH: 154px, IMG: '../img/sprite.png')));
$icons: map-merge($icons,('speed': (X: -116px, Y:-51px, W: 41px, H: 41px, TW: 203px, TH: 154px, IMG: '../img/sprite.png')));
$icons: map-merge($icons,('tv': (X: 0px, Y:0px, W: 50px, H: 46px, TW: 203px, TH: 154px, IMG: '../img/sprite.png')));
$icons: map-merge($icons,('user': (X: -167px, Y:0px, W: 36px, H: 44px, TW: 203px, TH: 154px, IMG: '../img/sprite.png')));


// Gets an attribute from the sass map
@function icon-attr($icon, $attr) {
  $icon: map-get($icons, $icon);
  @return map-get($icon, $attr); }

@mixin sprite($iconName) {
  background-image: url(icon-attr($iconName, IMG));
  width: icon-attr($iconName, W);
  height: icon-attr($iconName, H);
  background-position: icon-attr($iconName, X) icon-attr($iconName, Y); }
@mixin sprite-position($iconName) {
  background-position: icon-attr($iconName, X) icon-attr($iconName, Y); }
@mixin sprite-retina($iconName) {
  background-image: url(icon-attr($iconName, IMG));
  $width: icon-attr($iconName, W);
  $height: icon-attr($iconName, H);
  width: $width/2;
  height: $height/2;
  $x: icon-attr($iconName, X);
  $y: icon-attr($iconName, Y);
  background-position: $x/2 $y/2;
  $tw: icon-attr($iconName, TW);
  $th: icon-attr($iconName, TH);
  background-size: $tw/2 $th/2; }


@mixin s($i) {
  @include sprite($i); }
@mixin sp($i) {
  @include sprite-position($i); }
@mixin sr($i) {
  @include sprite-retina($i); }


