body {
  font-family: 'Lato'; }

.flex {
  display: flex; }

.container, .row {
  height: 100%; }


.heading {
  h2 {
    font-size: 30px;
    font-weight: bold;
    font-style: normal;
    font-stretch: normal;
    line-height: 0.87;
    letter-spacing: normal;
    text-align: left;
    color: $primary; } }

.btn {
  background: white;
  border: 2px solid $primary;
  padding: 13px 70px;
  color: black;
  font-size: 18px;
  text-decoration: none;
  border-radius: 3px;
  transition: all 0.3s;
  display: block;
  text-align: center;
  &:hover {
    background: $primary;
    color: white; } }

