.slider {
  overflow: hidden;
  height: 430px;
  box-shadow: 0px 11px 46px -5px rgba(30, 15, 0, 0.75);
  .container {
    position: relative; }
  &__block {
    display: flex;
    .row {
      height: inherit; }
    &__text {
      position: relative;
      color: white;
      width: 45%;
      display: flex;
      flex-direction: column;
      justify-content: center;
      padding: 0 84px;
      font-family: 'Blogger Sans';
      padding-top: 50px;
      &__sale {
        background: #2078c9;
        padding: 15px 25px;
        display: inline-block;
        position: absolute;
        right: 0;
        top: 0;
        span {
          color: white;
          font-size: 20px; } }
      h2 {
        font-size: 33px;
        margin-bottom: 25px;
        font-weight: normal; }
      &__prices {
        display: flex;
        justify-content: space-between;
        p {
          font-size: 95px;
          line-height: 75px;
          margin-bottom: 15px; }
        span {
          font-size: 33px;
          display: block;
          text-align: center; }
        .second {
          p {
            font-size: 65px;
            position: relative;
            bottom: -11px; } } }
      h4 {
        text-align: right;
        color: #1e6381;
        font-size: 24px;
        margin-top: 30px;
        font-weight: normal; } }
    &__image {
      width: 55%;
      img {
        @include object-fit(cover);
        width: 100%;
        height: 100%;
        display: block; } } } }


.owl {
  &-item {
    background: $primary;
    filter: blur(2px);
    transform: scale(1.1) !important;
    transition: all 0.3s;
    &.active {
      filter: blur(0px);
      transform: scale(1) !important;
      position: relative;
      z-index: 10; } } }

.next, .prev {
    position: absolute;
    z-index: 100;
    cursor: pointer;
    top: 50%;
    transform: translateY(-50%);
    color: $primary3;
    font-size: 50px; }
.next {
  right: -50px; }
.prev {
  left: -40px; }


.owl-carousel .owl-stage-outer {
  overflow: none !important; }
